<template>
    <span class="p-ml-2 p-mr-2"> Cycle: </span>
    <span class="info-text">{{ cycle ? cycle.cycleIdDisp : "--------" }}</span>
    <p-divider layout="vertical" />
    <span class="info-text">{{ cycle ? cycle.cycleType : "-----" }}</span>
    <p-divider layout="vertical" />
    <div v-tooltip.bottom="parterTooltip">
        <span class="p-mr-2"> Patient: </span>
        <span class="info-text">
            {{ patient.firstName ? patient.firstName : "----" }}
            {{ patient.lastName ? patient.lastName : "----" }}
        </span>
    </div>
    <p-divider layout="vertical" />
    <span class="info-text">
        {{ sex }}
    </span>
    <p-divider layout="vertical" />
    <span class="p-mr-2">Demographic No: </span>
    <span class="info-text">
        {{ patient.chart ? patient.chart : "--------" }}
    </span>
    <p-divider layout="vertical" />
    <span class="info-text">
        {{ patient.year ? patient.year : "----" }}/{{
            patient.month ? patient.month : "--"
        }}/
        {{ patient.day ? patient.day : "--" }}
    </span>
    <p-divider layout="vertical" />
    <span class="info-text">({{ patient ? patient.age : "--" }} yrs)</span>
    <p-divider layout="vertical" />
    <span class="info-text">{{ patient ? patient.doctor : "--" }}</span>
    <p-divider layout="vertical" />
    <span style="color: #2196F3" v-if="isBornCycle">Born</span>

    <p-divider v-if="patient.chart" layout="vertical" />
    <!-- <p-button
        outlined
        v-if="patient.chart"
        label="Encounter"
        @click="openPatientEncounter"
    /> -->
    <span class="p-buttonset" style="display: flex;">
        <p-button
            label="Encounter"
            class="p-button-sm"
            @click="openPatientEncounter('IN')"
        />
        <p-button
            class="p-button-sm"
            icon="pi pi-globe"
            @click="openPatientEncounter('OUT')"
        />
    </span>
</template>

<script>
import { computed } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();

        const cycle = computed(() => store.state.artCycle.selectedCycle);
        const patient = computed(() => store.state.artCycle.patient);
        const isBornCycle = computed(
            () => store.getters["artCycle/isBornCycle"]
        );

        const openPatientEncounter = (locality) => {
            if (locality == "IN") {
                const url =
                    "https://172.16.0.4/oscar/oscarEncounter/IncomingEncounter.do?" +
                    `providerNo=${
                        patient.value.doctorId
                    }&providerName=${encodeURIComponent(
                        patient.value.doctor
                    )}&demographicNo=${patient.value.chart}`;

                window.open(url);
            } else {
                const url =
                    "https://newlife.kai-oscar.com/oscar/oscarEncounter/IncomingEncounter.do?" +
                    `providerNo=${
                        patient.value.doctorId
                    }&providerName=${encodeURIComponent(
                        patient.value.doctor
                    )}&demographicNo=${patient.value.chart}`;

                window.open(url);
            }
        };

        const sex = computed(() => {
            if (store.state.artCycle.patient == null) {
                return "-";
            } else if (store.state.artCycle.patient.sex === "F") {
                return "Female";
            } else if (store.state.artCycle.patient.sex === "M") {
                return "Male";
            } else {
                return store.state.artCycle.patient.sex;
            }
        });
        const parterTooltip = computed(() => {
            let partner = store.state.artCycle.partner;

            if (partner) {
                return `Partner: <strong>${partner.firstName} ${partner.lastName}</strong>
                    Age: <strong>${partner.age}<strong>
                `;
            } else {
                return "Parner Not Available";
            }
        });

        return {
            cycle,
            patient,
            isBornCycle,
            parterTooltip,
            sex,
            openPatientEncounter
        };
    }
};
</script>

<style scoped>
span {
    font-size: 0.9em;
    font-weight: 500;
}
.info-text {
    color: #800000;
}
</style>
